Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none;\n}\n",".glide{position:relative;width:100%;box-sizing:border-box}.glide *{box-sizing:inherit}.glide__track{overflow:hidden}.glide__slides{position:relative;width:100%;list-style:none;backface-visibility:hidden;transform-style:preserve-3d;touch-action:pan-Y;overflow:hidden;padding:0;white-space:nowrap;display:flex;flex-wrap:nowrap;will-change:transform}.glide__slides--dragging{user-select:none}.glide__slide{width:100%;height:100%;flex-shrink:0;white-space:normal;user-select:none;-webkit-touch-callout:none;-webkit-tap-highlight-color:transparent}.glide__slide a{user-select:none;-webkit-user-drag:none;-moz-user-select:none;-ms-user-select:none}.glide__arrows{-webkit-touch-callout:none;user-select:none}.glide__bullets{-webkit-touch-callout:none;user-select:none}.glide--rtl{direction:rtl}\n","@define-mixin atSmall {\n  @media (min-width: 530px) {\n    @mixin-content;\n  }\n}\n\n@define-mixin atSmallMedium {\n  @media (min-width: 767px) {\n    @mixin-content;\n  }\n}\n\n@define-mixin atMedium {\n  @media (min-width: 960px) {\n    @mixin-content;\n  }\n}\n\nbody {\n  color: #333;\n  font-family: 'Roboto', sans-serif;\n  overflow-x: hidden;\n  position: relative;\n}\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n\na {\n  color: $mainBlue;\n}\n\na:hover {\n  text-decoration: none;\n}\n\np, ul, ol {\n  margin: 0 0 1.65em 0;\n}\n\np, li {\n  line-height: 1.65;\n}",".container {\n  max-width: 1300px;\n  margin: 0 auto;\n  padding: 0 16px;\n  position: relative;\n}\n\n.container--narrow {\n  max-width: 960px;\n}\n\n.section-break {\n  border: none;\n  border-top: 1px solid #DDD;\n  margin: 2.5rem 0;\n}\n\n.min-list {\n  padding: 0;\n  margin: 0;\n}\n\n.min-list li {\n  list-style: none;\n}\n\n.p-top-small {padding-top: 1rem}\n.p-top-large {padding-top: 1.6rem}\n.p-bottom-large {padding-bottom: 1.6rem}\n\n@mixin atSmall {\n  .p-top-large {padding-top: 3.5rem}\n  .p-bottom-large {padding-bottom: 3.5rem}\n}\n\n.no-margin {margin: 0}\n.nu {text-decoration: none}\n.nu:hover {text-decoration: underline}\n\n.t-left {text-align: left}\n.t-right {text-align: right}\n.t-center {text-align: center}\n\n\n.t-small {font-size: .85rem}\n\n.float-left {float: left}\n.float-right {float: right}\n.push-right {margin-right: 20px}\n\n.container:before,\n.container:after,\n.group:before,\n.group:after {\n  content: \" \";\n  displ